<template>
  <div class="booking">
         <div class="text-center my-10" v-if="firstload">
        <v-progress-circular
      indeterminate
      color="primary"
    ></v-progress-circular>
         </div>
         <div v-if="car">
    <v-card :loading="loading" class="mx-auto my-12" max-width="1000">
          <v-alert type="success" v-if="success">
      Booking sucessful, we will contact you later!
    </v-alert>
        <v-alert type="error" v-if="error">
      There is an error with your booking. Try again!
    </v-alert>
      <template slot="progress">
        <v-progress-linear
          color="deep-purple"
          height="10"
          indeterminate
        ></v-progress-linear>
      </template>

      <v-img height="250" v-bind:src="car.picUrl" contain></v-img>

      <v-card-title>{{ car.model }}</v-card-title>

      <v-card-text>
        <v-row align="center" class="mx-0">
          <div class="grey--text m-4">
            {{ car.location }}
          </div>
        </v-row>

        <div class="my-4 text-subtitle-1">RM {{ car.price }}</div>

        <div>{{ car.description }}</div>
      </v-card-text>

      <v-divider class="mx-4"></v-divider>

      <v-card-title>Booking information</v-card-title>
      <v-text-field label="Name" v-model="name"></v-text-field>
      <v-text-field label="Email" v-model="email"></v-text-field>
      <v-text-field label="Phone Number" v-model="phone"></v-text-field>
      <v-textarea label="Message" v-model="message"></v-textarea>
      <v-btn v-on:click="bookPressed" v-bind:disabled="loading"><span v-if="!loading">Book now</span><v-progress-circular
      indeterminate color="primary" class="ml-3" v-if="loading"
    ></v-progress-circular></v-btn>
    </v-card>
  </div>
  </div>
</template>
<script>
export default {
  name: "Booking",
  data() {
    return {
        car:null,
        success:false,
        error:false,
        firstload:false,
        loading:false,
        name:"",
        email:"",
        message:"",
        phone:""
    };
  },
  mounted() {
    let carId = this.$route.params.id;
    this.firstload=true;
    console.log(carId);
    let url = `https://api.sheety.co/a81b8717bbfe9b9f8277ccb9ae4ab0d8/carRental/sheet1/${carId}`;
    fetch(url)
      .then((response) => response.json())
      .then((json) => {
        // Do something with the data
        this.car = json["sheet1"];
        this.firstload=false;
      });
  },
  methods:{
      bookPressed:function(){
        this.success=false
        this.error=false;
        this.loading=true
        let url = 'https://api.sheety.co/a81b8717bbfe9b9f8277ccb9ae4ab0d8/carRental/sheet2';
  let body = {
    sheet2: {
      name:this.name,
      email:this.email,
      message:this.message,
      phone:this.phone,
      carModel:this.car.model
    }
  };
  fetch(url, {
    method: 'POST',
    body: JSON.stringify(body),
    headers: {
    'Content-Type': 'application/json'
    },
  })
  .then((response) => response.json())
  .then(json => {
    // Do something with object
    console.log(json.sheet2);
    this.loading=false
    if (json.sheet2){
    this.success=true
    }
    else{
      this.error=true
    }
  }).catch(err=>{
    console.log(err)
    this.error=true
  })
  }
  }
};
</script>
<style>
</style>
