      :key="i"
      :src="item.src"
      reverse-transition="fade-transition"
      transition="fade-transition"
    ></v-carousel-item>
      </v-carousel>
    </section>
    <section id="featured">
      <v-container>
        <div class="text-center my-10" v-if="loading">
        <v-progress-circular
