        <v-row>
          <v-col v-for="car in cars" :key="car" cols="4">
            <v-card :loading="loading" class="mx-auto my-12" max-width="374">
              <template slot="progress">
                <v-progress-linear
                  color="deep-purple"
                  height="10"
                  indeterminate
                ></v-progress-linear>
              </template>

              <v-img height="250" v-bind:src="car.picUrl"></v-img>

              <v-card-title>{{ car.model }}</v-card-title>

