<script>
export default {
  name: "Home",

  components: {},
  mounted() {
    this.loading=true
    let url =
      "https://api.sheety.co/a81b8717bbfe9b9f8277ccb9ae4ab0d8/carRental/sheet1";
    fetch(url)
      .then((response) => response.json())
      .then((json) => {
        // Do something with the data
        console.log(json.sheet1S);
        this.cars = json["sheet1"];
        this.loading=false
      });
  },
  data() {
    return {
      loading:false,
      cars: [],
        items: [
          {
            src: require("@/assets/car1.jpg"),
          },
          {
            src: require("@/assets/car2.jpg"),
          },
          {
            src: require("@/assets/car3.jpg"),
          },
        ],
    };
  },
  methods:{
    openDetail:function(id){
      this.$router.push(`booking/${id}`)
    }
  }
};
</script>
